*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
}

/*This is here because react-anchor-link-smooth-scroll and styled-components don't play nice together*/

a[href^="#"] {
  margin: 5rem;
  letter-spacing: .2rem;
  font-size: 2rem;
}

a[href^="#"]:link, a[href^="#"]:visited {
  color: rgb(242,242,242);
  text-decoration: none;
}

a.header-anchor  {
  display: inline-block;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translateX(-50%);
  letter-spacing: .3rem;
  color: rgb(240,240,240);
  font-family: 'Play', sans-serif;
  font-size: 2.5rem;
  border: .3rem solid rgb(245,245,245);
  border-width: 0 0 .2rem 0;
  padding: .5rem 1rem .7rem 1rem;
  background-color: transparent;
  font-weight: 10;
  cursor: pointer;
  margin: 0;
  letter-spacing: .2rem;
  transition: all .4s;
}

a.header-anchor:hover {
  transform: translateX(-50%) scale(1.1);
}